import React, { useState, useEffect, useRef } from 'react';
import data from '../../lib/data';

const JobContext = React.createContext();

const JobContextProvider = ({ children }) => {
  const [state, setState] = useState();

  const isInitialMount = useRef(true);

  useEffect(() => {
    if (isInitialMount.current) {
      isInitialMount.current = false;
      const initialState = JSON.parse(localStorage.getItem('data')) || data;
      setState(initialState);
    } else {
      localStorage.setItem('data', JSON.stringify(state));
    }
  });

  const onDragEnd = result => {
    const { destination, source, draggableId } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    const start = state.columns[source.droppableId];
    const finish = state.columns[destination.droppableId];

    if (start === finish) {
      const newJobIds = Array.from(start.jobs);

      newJobIds.splice(source.index, 1);
      newJobIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...start,
        jobs: newJobIds
      };

      const newState = {
        ...state,
        columns: {
          ...state.columns,
          [newColumn.id]: newColumn
        }
      };

      setState(newState);
      return;
    }

    const startJobIds = Array.from(start.jobs);
    startJobIds.splice(source.index, 1);

    const newStart = {
      ...start,
      jobs: startJobIds
    };

    const finishJobIds = Array.from(finish.jobs);
    finishJobIds.splice(destination.index, 0, draggableId);

    const newFinish = {
      ...finish,
      jobs: finishJobIds
    };

    const newState = {
      ...state,
      columns: {
        ...state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish
      }
    };

    setState(newState);
  };

  const modalTrigger = id => {
      console.log
    setState({ ...state, isModalOpen: !state.isModalOpen });
  };

  return (
    <JobContext.Provider value={{ ...state, onDragEnd, modalTrigger }}>
      {children}
    </JobContext.Provider>
  );
};

export default JobContextProvider;
export { JobContext };
